.header {
  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;

  width: 100%;
  height: 60px;

  background-color: #20232a;
  color: #fff;

  display: flex;
  flex-direction: row;
}
.logo-container {
  display: flex;

  width: calc(100% / 6);
  height: 100%;
  margin-right: 10px;

  color: #61dafb;
  text-decoration: transparent;

  align-items: center;
}
.logo {
  max-width: 22.48px;
}
.logo-text {
  margin-left: 10px;

  font-size: 20px;
  line-height: 20px;
  font-weight: 700;

  color: inherit;
}
.header-content {
  display: flex;
  flex-direction: row;

  padding-right: 20px;
  padding-left: 20px;
  margin-left: auto;
  margin-right: auto;
}
@media (min-width: 1340px) {
  .header-content {
    max-width: 1260px;
  }
}
@media (min-width: 780px) {
  .header-content {
    width: 90%;
  }
}
@media (min-width: 600px) and (max-width: 1279px){
  .header-content {
    height: 50px;
  }
}
.header-nav {
  width: calc(100% / 2);
  display: flex;
  flex-direction: row;
  align-items: stretch;

  height: 100%;
}
.header-link {
  display: flex;
  padding-left: 15px;
  padding-right: 15px;
  align-items: center;

  font-size: 18px;
  font-weight: 300;
  text-decoration: none;

  color: #ffffff;
}
.header-link:hover {
  color: #61dafb;
}
.header-right {
  display: flex;
  flex-direction: row;
}
.header-right > .header-link {
  font-size: 14px;
  font-weight: 500;
  padding: 5px 10px;
}
.header-form {
  width: calc(100% / 5);
  display: flex;
  flex-direction: row;
  align-items: center;
}
.header-search-container {
  position: relative;
  display: flex;
  direction: ltr;

  width: 100%;
  height: 100%;
  align-items: center;
}
.header-search {
  min-width: 100px;
  width: 100%;

  border: 0;
  padding: 4px 4px 4px 29px;

  color: #ffffff;
  font-size: 18px;
  font-weight: 300;

  background: transparent;
  background-image: url("https://reactjs.org/search.svg");
  background-size: 16px 16px;
  background-repeat: no-repeat;
  background-position-x: 4px;
  background-position-y: center;
}
input[type=search] {
  outline-offset: -2px;
}
input[type=search]:focus {
  border: 0;
  outline: none;
  background-color: #373940;
  border-radius: 0.25em;
}
